div.m-area.m-top.m-left>div.m-panel.ign-search-panel.collapsed>button,
div.m-area.m-bottom.m-left>div.m-panel.ign-search-panel.collapsed>button,
div.m-area.m-top.m-right>div.m-panel.ign-search-panel.collapsed>button,
div.m-area.m-bottom.m-right>div.m-panel.ign-search-panel.collapsed>button,
.ign-search-panel .g-cartografia-flecha-izquierda,
.g-cartografia-flecha-derecha {
    color: white !important;
    background: #71a7d3 !important;
}

.m-ignsearch {
    white-space: nowrap;
}

.ign-search-panel {
    order: -100;
}

#div-contenedor.m-ignsearch .search-panel {
    order: -100;
}

.m-ignsearch .search-panel.collapsed {
    height: 40px;
}

.m-ignsearch .search-panel * {
    color: black;
}

.m-ignsearch .search-panel {
    background-color: white;
}

/* Loading animation */

#m-ignsearch-results {
    align-items: center;
    background-color: white;
    display: flex;
    position: absolute;
    width: 100%;
}

div.m-area.m-bottom>div>div #div-contenedor>#m-ignsearch-results {
    bottom: 40px;
}

#m-ignsearch-results::before {
    margin: 0 auto;
    -webkit-animation: mloading 1s linear infinite;
    -moz-animation: mloading 1s linear infinite;
    animation: mloading 1s linear infinite;
}

/* Other */

.m-areas>div.m-area>div.m-panel.ign-search-panel.opened {
    background-color: transparent !important;
    box-shadow: none;
    z-index: 2000;
}

.m-ignsearch .search-panel {
    height: 40px;
}

.m-ignsearch #m-ignsearch-results #m-ignsearch-results-list {
    list-style-position: outside;
    list-style-type: none;
    overflow: hidden;
    padding-left: 0.5em;
    width: 100%;
}

/* former text color: #7A7A73; */

.m-ignsearch #m-ignsearch-results #m-ignsearch-results-list li {
    color: black;
    line-height: 15px;
    margin: 5px 0;
    overflow-wrap: break-word;
    padding-top: 3px;
    white-space: pre-line;
}

.m-ignsearch #m-ignsearch-results #m-ignsearch-results-list li+span {
    color: #7A7A73;
}

.m-ignsearch #m-ignsearch-results #m-ignsearch-results-list li:hover {
    background-color: #71a7d3;
    color: white;
    cursor: pointer;
    padding-left: 1em;
}

#m-ignsearch-search-input {
    width: 300px;
    border: none;
}

#m-ignsearch-clear-button,
#m-ignsearch-locate-button {
    background-color: white;
    border: none;
    margin-right: 1em;
}

#m-ignsearch-locate-button {
    font-family: g-plugin-ignsearch;
}

#m-ignsearch-locate-button:focus {
    outline: none;
}

button#m-ignsearchlocator-locate-button {
    color: #7A7A73;
    font-size: 1.4em;
}

.m-ignsearch .search-panel .m-button-limpiar {
    border: none;
    padding: 0;
}

.m-ignsearch .search-panel button:hover {
    cursor: pointer;
}

.m-ignsearch .search-panel button span {
    color: #7A7A73;
    font-size: 1.4em;
}

.m-ignsearch .search-panel .g-cartografia-papelera::before {
    vertical-align: bottom;
}

.ignsearch-popup {
    width: 250px;
}

/*.ign-search-panel>.g-cartografia-flecha-izquierda {
    display: none;
}*/

div.m-area.m-left>div.m-panel.ign-search-panel.opened>button {
    position: absolute;
    right: -40px;
}

div.m-area.m-right>div.m-panel.ign-search-panel.opened>button {
    position: absolute;
    left: -40px;
}

.m-ignsearch>.search-panel>button#m-ignsearch-locate-button,
.m-ignsearch>.search-panel>button#m-ignsearch-clear-button {
    vertical-align: middle;
}

#m-ignsearch-results>p {
    text-align: center;
    white-space: pre-wrap;
}

div.m-control.m-container.m-ignsearch {
    display: block !important;
}

/* MEDIA QUERIES */

@media only screen and (min-width: 769px) {

    .search-panel {
        display: flex;
        justify-content: space-between;
    }
}

@media only screen and (max-width: 768px) {

    .m-ignsearch #m-ignsearch-results #m-ignsearch-results-list {
        height: 200px;
        overflow-y: scroll;
    }
}

@media only screen and (min-width: 411px) and (max-width: 600px) {
    div.m-control.m-container.m-ignsearch {
        width: 300px;
    }

    .m-ignsearch #m-ignsearch-search-input {
        width: 65%;
    }
}

@media only screen and (max-width: 410px) {
    div.m-panel.ign-search-panel.opened {
        width: 85% !important;
    }

    .m-ignsearch #m-ignsearch-search-input {
        width: 50%;
    }
}

@font-face {
    font-family: 'ignsearch';
    src: url(data:application/vnd.ms-fontobject;base64,);
    src: url(data:application/vnd.ms-fontobject;base64,#iefix) format('embedded-opentype'),
        url(data:font/ttf;base64,) format('truetype'),
        url(data:font/woff;base64,) format('woff'),
        url(data:image/svg+xml;base64,#lupa) format('svg');
    font-weight: normal;
    font-style: normal;
    font-display: block;
}

div.m-areas>div.m-area>div.m-panel.ign-search-panel.collapsed>button.m-panel-btn {
    font-family: 'ignsearch' !important;
    /* speak: none; */
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.m-panel .m-ign-search-panel [class^="icon-"]:before,
.m-panel .m-ign-search-panel [class*=" icon-"]:before {
    font-family: "ignsearch" !important;
    font-style: normal;
    font-weight: normal;
    speak: none;
    display: inline-block;
    text-decoration: inherit;
    width: 1em;
    margin-right: .2em;
    text-align: center;
    font-variant: normal;
    text-transform: none;
    line-height: 1em;
    margin-left: .2em;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.icon-lupa:before {
    content: "\e99e";
}
