@font-face {
    font-family: 'g-plugin-ignsearch';
    src: url(data:application/vnd.ms-fontobject;base64,);
    src: url(data:application/vnd.ms-fontobject;base64,#iefix) format('embedded-opentype'),
        url(data:font/ttf;base64,) format('truetype'),
        url(data:font/woff;base64,) format('woff'),
        url(data:image/svg+xml;base64,#g-plugin-ignsearch) format('svg');
    font-weight: normal;
    font-style: normal;
}

[class^="g-plugin-ignsearch-"],
[class*=" g-plugin-ignsearch-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'g-plugin-ignsearch' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.g-plugin-ignsearch-localizacion3:before {
    content: "\e9900";
}